.item {
 width: 80%;
 padding: 50px;
 margin: 0 auto;
 border-bottom: 2px solid tomato;
}

.item h1 {
    text-align: center;
    
}
 
.item img {
    top:20px;
    position: relative;
    transform: translateY(0);
    transition: transform 0.5s;
    z-index: 1;
    margin: 0 auto;
}

.item img::after {
    content: "";
    display: block;
    position: absolute;
    bottom: -30px;
    left: 50%;
    height: 8px;
    width: 100%;
    box-shadow: 0px 0px 15px 0px rgba(219, 33, 33, 0.4);
    border-radius: 50%;
    background-color: rgba(231, 29, 29, 0.2);
    transform: translate(-50%, 0);
    transition: transform 1s;
}

.item img:hover {
    transform: translateY(-40px);
    transition: transform 0.5s;
}

.item img:hover::after {
    transform: translate(-50%, 40px) scale(0.75);
    transition: transform 1s;
}

.item p {
    transform: translateY(-150%);
    z-index: 0;
    font-family:Arial, Helvetica, sans-serif;
    color: red;
}
.content {
    /* width: 70%; */
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    /* margin: 0 auto; */

}

.description {
    max-width: 400px;
    width: 100%;
    margin-top: 20px;
    height: 274px;
    overflow-y: scroll;
    border: 3px solid black;
    color:red;
    padding: 10px;
    
}
.description::-webkit-scrollbar{
    display: none;
}
.imgAndCalories {
    width: 50%;
}